<template>
  <v-card>
    <v-card-title class="d-flex align-center justify-start pl-2"
      ><v-btn variant="plain" color="primary" icon @click="show = !show">
        <v-icon>{{ show ? "mdi-chevron-down" : "mdi-chevron-right" }}</v-icon>
        <v-tooltip activator="parent" location="left">
          <span>{{ show ? "Ocultar Tipos" : "Mostrar Tipos" }}</span>
        </v-tooltip>
      </v-btn>
      <v-icon>mdi-bug</v-icon>
      <span class="ml-3">Inimigos</span></v-card-title
    >
    <v-divider></v-divider>
    <v-expand-transition>
      <div v-show="show">
        <v-fab
          :active="show"
          class="mr-10"
          color="success"
          location="top end"
          absolute
          offset
          append-icon="mdi-plus"
          @click="openDialog"
        >
          <span>Novo Tipo</span></v-fab
        >
        <v-card-text>
          <v-row v-if="isLoading">
            <v-col cols="12" class="d-flex justify-center">
              <v-progress-circular
                indeterminate
                color="primary"
              ></v-progress-circular>
            </v-col>
          </v-row>
          <v-row class="pa-5" v-else>
            <v-col
              cols="12"
              v-for="enemytype in enemytypes"
              :key="enemytype.enemytype_id"
            >
              <EnemiesConfig
                :enemytype="enemytype"
                @refresh="getEnemyTypes()"
              />
            </v-col>
          </v-row>
        </v-card-text>
      </div>
    </v-expand-transition>
    <v-dialog v-model="dialog" @afterLeave="reset" width="auto">
      <v-card class="mx-auto" min-width="500">
        <v-card-title class="bg-success">
          <span class="text-h6">Novo Tipo de Inimigo</span>
        </v-card-title>
        <v-card-text class="d-flex align-center justify-center">
          <v-sheet width="400">
            <v-form class="mt-4" v-model="formValid">
              <v-row justify="center">
                <v-col cols="10">
                  <v-text-field
                    v-model.trim="newEnemyType.enemytype_name"
                    label="Nome"
                    placeholder="Insira o nome"
                    prepend-inner-icon="mdi-tag-text-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-sheet>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="dialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingAdd"
            :disabled="!formValid"
            @click="postEnemyType()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import EnemiesConfig from "./EnemiesConfig.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "EnemyTypesConfig",
  components: {
    EnemiesConfig,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      show: false,
      enemytypes: [],
      isLoading: false,

      dialog: false,
      newEnemyType: {},
      formValid: false,
      isLoadingAdd: false,

      rules: {
        required: (value) => !!value || "Campo obrigatório",
        sizeSmall: (value) => value?.length <= 30 || "Máximo de 30 caracteres",
      },
    };
  },
  watch: {
    show(newVal) {
      if (newVal && this.enemytypes.length === 0) {
        this.getEnemyTypes();
      }
    },
  },
  methods: {
    async getEnemyTypes() {
      this.isLoading = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/enemytypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.enemytypes = response.data;
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao buscar tipos de inimigos");
        });
    },
    openDialog() {
      this.dialog = true;
    },
    reset() {
      this.newEnemyType = {};
      this.formValid = false;
    },
    async postEnemyType() {
      this.isLoadingAdd = true;
      let payload = {
        enemytype_name: this.newEnemyType.enemytype_name.trim(),
      };
      await axios
        .request({
          url: "api/enemytypes",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingAdd = false;
          this.generalStore.successSnackbar(
            "Tipo de inimigo criado com sucesso"
          );
          this.dialog = false;
          this.getEnemyTypes();
        })
        .catch((error) => {
          console.log(error);
          this.generalStore.errorSnackbar("Erro ao criar tipo de inimigo");
        });
    },
  },
};
</script>

<style></style>
