<template>
  <v-card :loading="isLoading">
    <v-card-title class="d-flex align-center justify-space-between pl-2">
      <div class="d-flex align-center">
        <v-btn variant="plain" color="primary" icon @click="show = !show">
          <v-icon>{{ show ? "mdi-chevron-down" : "mdi-chevron-right" }}</v-icon>
          <v-tooltip activator="parent" location="left">
            <span>{{ show ? "Ocultar Culturas" : "Mostrar Culturas" }}</span>
          </v-tooltip>
        </v-btn>
        <span>{{ croptype.croptype_name }}</span>
        <v-btn color="warning" variant="text" icon @click="openEditDialog">
          <v-icon>mdi-pencil</v-icon>
          <v-tooltip activator="parent" location="bottom">Editar</v-tooltip>
        </v-btn>
      </div>
      <div>
        <v-btn
          color="error"
          variant="text"
          icon
          @click="deleteCropType"
          :loading="isLoadingDelete"
        >
          <v-icon>mdi-close</v-icon>
          <v-tooltip activator="parent" location="bottom">Apagar</v-tooltip>
        </v-btn>
      </div>
    </v-card-title>
    <v-divider></v-divider>
    <v-expand-transition>
      <div v-show="show">
        <v-fab
          :active="show"
          class="mr-10"
          color="success"
          location="top end"
          absolute
          offset
          append-icon="mdi-plus"
          @click="openDialog"
        >
          <span>Nova Cultura</span>
        </v-fab>
        <v-card-text>
          <v-row v-if="isLoading">
            <v-col cols="12" class="d-flex justify-center">
              <v-progress-circular
                indeterminate
                color="primary"
              ></v-progress-circular>
            </v-col>
          </v-row>
          <v-row class="pa-5" v-else-if="crops.length > 0">
            <v-col cols="12" v-for="crop in crops" :key="crop.crop_id">
              <VarietyConfig :crop="crop" @refresh="getCrops" />
            </v-col>
          </v-row>
          <v-row class="pa-5" v-else>
            <v-col cols="12" class="d-flex justify-center">
              <span class="text-overline">Sem culturas</span>
            </v-col>
          </v-row>
        </v-card-text>
      </div>
    </v-expand-transition>
    <v-dialog v-model="dialog" @afterLeave="reset" width="auto">
      <v-card class="mx-auto" min-width="500">
        <v-card-title class="bg-success">
          <span class="text-h6">Nova Cultura</span>
        </v-card-title>
        <v-card-text class="d-flex align-center justify-center">
          <v-sheet width="400">
            <v-form class="mt-4" v-model="formValid">
              <v-row justify="center">
                <v-col cols="10">
                  <v-text-field
                    v-model.trim="newCrop.crop_name"
                    label="Nome Comum"
                    placeholder="Insira o nome comum"
                    prepend-inner-icon="mdi-tag-text-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
                <v-col cols="10">
                  <v-text-field
                    v-model.trim="newCrop.crop_scientificname"
                    label="Nome Científico"
                    placeholder="Insira o nome científico"
                    prepend-inner-icon="mdi-flask-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-sheet>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="dialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingAdd"
            :disabled="!formValid"
            @click="postCrop()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="editDialog" @afterLeave="resetEdit" width="auto">
      <v-card class="mx-auto" min-width="500">
        <v-card-text class="d-flex align-center justify-center">
          <v-sheet width="400">
            <v-form class="mt-4" v-model="editFormValid">
              <v-row justify="center">
                <v-col cols="10">
                  <v-text-field
                    v-model.trim="cropTypeEdit.croptype_name"
                    label="Nome"
                    placeholder="Insira o nome"
                    prepend-inner-icon="mdi-tag-text-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-sheet>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="editDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingEdit"
            :disabled="!canEdit"
            @click="editCropType()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import VarietyConfig from "./VarietyConfig.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "CropsVarietiesConfig",
  components: {
    VarietyConfig,
  },
  props: {
    croptype: {
      type: Object,
      required: true,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },

  data() {
    return {
      show: false,
      crops: [],
      isLoading: false,

      dialog: false,
      newCrop: {},
      formValid: false,
      isLoadingAdd: false,

      editDialog: false,
      cropTypeEdit: {},
      editFormValid: false,
      isLoadingEdit: false,

      isLoadingDelete: false,

      rules: {
        required: (value) => !!value || "Campo obrigatório",
        sizeSmall: (value) => value?.length <= 30 || "Máximo de 30 caracteres",
      },
    };
  },
  watch: {
    show(newVal) {
      if (newVal && this.crops.length === 0) {
        console.log("get crops");
        this.getCrops();
      }
    },
  },
  computed: {
    canEdit() {
      return (
        this.editFormValid &&
        this.cropTypeEdit.croptype_name.trim() !==
          this.croptype.croptype_name.trim()
      );
    },
  },
  methods: {
    async getCrops() {
      this.isLoading = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/croptypes/" + this.croptype.croptype_id + "/crops",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.crops = response.data;
          this.isLoading = false;
        })
        .catch((error) => {
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao obter culturas.");
        });
    },
    openDialog() {
      this.dialog = true;
      this.formValid = false;
    },
    reset() {
      this.newCrop = {};
    },
    async postCrop() {
      this.isLoadingAdd = true;
      let payload = {
        croptype_id: this.croptype.croptype_id,
        crop_name: this.newCrop.crop_name.trim(),
        crop_scientificname: this.newCrop.crop_scientificname.trim(),
      };
      await axios
        .request({
          url: "api/crops",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then(() => {
          this.isLoadingAdd = false;
          this.dialog = false;
          this.generalStore.successSnackbar("Cultura adicionada com sucesso");
          this.getCrops();
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingAdd = false;
          this.generalStore.errorSnackbar("Erro ao adicionar cultura");
        });
    },
    openEditDialog() {
      this.editDialog = true;
      this.editFormValid = false;
      this.cropTypeEdit = { ...this.croptype };
    },
    resetEdit() {
      this.cropTypeEdit = {};
    },
    editCropType() {
      this.isLoadingEdit = true;
      let payload = {
        croptype_name: this.cropTypeEdit.croptype_name.trim(),
      };
      axios
        .request({
          url: "api/croptypes/" + this.cropTypeEdit.croptype_id,
          method: "PATCH",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then(() => {
          this.isLoadingEdit = false;
          this.editDialog = false;
          this.generalStore.successSnackbar(
            "Tipo de cultura editado com sucesso"
          );
          this.$emit("refresh");
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingEdit = false;
          this.generalStore.errorSnackbar("Erro ao editar tipo de cultura");
        });
    },
    async deleteCropType() {
      this.isLoadingDelete = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/croptypes/" + this.croptype.croptype_id,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then(() => {
          this.isLoadingDelete = false;
          this.generalStore.successSnackbar(
            "Família de culturas apagada com sucesso"
          );
          this.$emit("refresh");
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingDelete = false;
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },
  },
};
</script>

<style></style>
