<template>
  <v-card :loading="isLoading">
    <v-card-title class="d-flex align-center justify-start pl-2">
      <v-btn variant="plain" color="primary" icon @click="show = !show">
        <v-icon>{{ show ? "mdi-chevron-down" : "mdi-chevron-right" }}</v-icon>
        <v-tooltip activator="parent" location="left">
          <span>{{ show ? "Ocultar Tipos" : "Mostrar Tipos" }}</span>
        </v-tooltip>
      </v-btn>
      <v-icon>mdi-multimedia</v-icon>
      <span class="ml-3">Tipos de Mídia</span>
    </v-card-title>
    <v-divider></v-divider>
    <v-expand-transition>
      <div v-show="show">
        <v-fab
          :active="show"
          class="me-4"
          color="success"
          location="top end"
          absolute
          offset
          append-icon="mdi-plus"
          @click="openAddDialog"
        >
          <span>Novo tipo</span>
        </v-fab>
        <v-card-text>
          <div v-if="isLoading">
            <v-row>
              <v-col cols="12" class="d-flex justify-center">
                <v-progress-circular
                  indeterminate
                  color="primary"
                ></v-progress-circular>
              </v-col>
            </v-row>
          </div>
          <div v-else-if="mediatypes.length > 0 && !isLoading">
            <v-list lines="1" class="py-0">
              <div
                v-for="(mediatype, index) in mediatypes"
                :key="mediatype.mediatype_id"
              >
                <v-list-item>
                  <v-list-item-title
                    class="d-flex align-center justify-space-between"
                  >
                    <div class="d-flex align-center">
                      <span class="text-subtitle-1 font-weight-medium">{{
                        mediatype.mediatype_label
                      }}</span>
                      <v-btn
                        color="warning"
                        variant="text"
                        icon
                        size="small"
                        @click="openEditTypeDialog(mediatype)"
                      >
                        <v-icon>mdi-pencil</v-icon>
                        <v-tooltip activator="parent" location="bottom"
                          >Editar</v-tooltip
                        >
                      </v-btn>
                    </div>
                    <div>
                      <v-btn
                        color="error"
                        variant="text"
                        icon
                        size="small"
                        @click="deleteMediaType(mediatype)"
                        :loading="mediatype.loadingDelete"
                      >
                        <v-icon>mdi-close</v-icon>
                        <v-tooltip activator="parent" location="bottom"
                          >Apagar</v-tooltip
                        >
                      </v-btn>
                    </div>
                  </v-list-item-title>
                  <v-list-item-subtitle>
                    <span>{{ mediatype.mediatype_name }}</span>
                  </v-list-item-subtitle>
                </v-list-item>
                <v-divider v-show="index != mediatypes.length - 1"></v-divider>
              </div>
            </v-list>
          </div>
          <div v-else-if="mediatypes.length == 0 && !isLoading">
            <v-row>
              <v-col cols="12" class="d-flex justify-center">
                <span>Nenhum tipo de mídia registado.</span>
              </v-col>
            </v-row>
          </div>
        </v-card-text>
      </div>
    </v-expand-transition>
    <v-dialog v-model="dialog" @afterLeave="reset" width="auto">
      <v-card class="mx-auto" min-width="500">
        <v-card-title v-if="!editMode" class="bg-success">
          <span>Novo tipo de Mídia</span>
        </v-card-title>
        <v-card-text class="d-flex align-center justify-center"
          ><v-sheet width="500">
            <v-form class="mt-4" v-model="formValid">
              <v-row justify="center">
                <v-col cols="4">
                  <v-text-field
                    v-model.trim="mediaType.mediatype_label"
                    label="Rótulo"
                    placeholder="Insira o rótulo"
                    prepend-inner-icon="mdi-tag-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.oneWord, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
                <v-col cols="8">
                  <v-text-field
                    v-model.trim="mediaType.mediatype_name"
                    label="Nome"
                    placeholder="Insira o nome"
                    prepend-inner-icon="mdi-tag-text-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form> </v-sheet
        ></v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="dialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingDialog"
            :disabled="editMode ? !canEditType : !formValid"
            @click="() => (editMode ? editMediaType() : postMediaType())"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "MediaTypesConfig",
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      show: false,
      mediatypes: [],
      isLoading: false,

      editMode: false,

      dialog: false,
      mediaType: {},
      formValid: false,
      isLoadingDialog: false,

      oldMediaType: {},

      rules: {
        required: (value) => !!value || "Campo obrigatório",
        sizeSmall: (value) => value.length <= 50 || "Máximo de 30 caracteres",
        oneWord: (value) =>
          /^\w+$/.test(value) || "Apenas uma palavra é permitida",
      },
    };
  },
  watch: {
    show(newVal) {
      if (newVal && this.mediatypes.length === 0) {
        this.getMediaTypes();
      }
    },
  },
  computed: {
    canEditType() {
      return (
        (this.formValid &&
          this.mediaType.mediatype_label.trim() !==
            this.oldMediaType.mediatype_label.trim()) ||
        this.mediaType.mediatype_name.trim() !==
          this.oldMediaType.mediatype_name.trim()
      );
    },
  },
  methods: {
    async getMediaTypes() {
      this.isLoading = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/mediatypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.mediatypes = response.data;
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao obter tipos de mídia.");
        });
    },
    openAddDialog() {
      this.editMode = false;
      this.dialog = true;
    },
    reset() {
      this.mediaType = {};
      this.oldMediaType = {};
      this.formValid = false;
      this.isLoadingDialog = false;
    },
    async postMediaType() {
      this.isLoadingDialog = true;
      let payload = {
        mediatype_label: this.mediaType.mediatype_label.trim(),
        mediatype_name: this.mediaType.mediatype_name.trim(),
      };
      await axios
        .request({
          url: "api/mediatypes",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingDialog = false;
          this.generalStore.successSnackbar(
            "Tipo de mídia adicionado com sucesso."
          );
          this.dialog = false;
          this.getMediaTypes();
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingDialog = false;
          this.generalStore.errorSnackbar("Erro ao adicionar tipo de mídia.");
        });
    },

    openEditTypeDialog(mediatype) {
      this.editMode = true;
      this.mediaType = { ...mediatype };
      this.oldMediaType = { ...mediatype };
      this.dialog = true;
    },

    async editMediaType() {
      this.isLoadingDialog = true;
      let payload = {
        mediatype_label: this.mediaType.mediatype_label.trim(),
        mediatype_name: this.mediaType.mediatype_name.trim(),
      };
      await axios
        .request({
          url: `api/mediatypes/${this.mediaType.mediatype_id}`,
          method: "PATCH",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingDialog = false;
          this.generalStore.successSnackbar(
            "Tipo de mídia atualizado com sucesso."
          );
          this.dialog = false;
          this.getMediaTypes();
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingDialog = false;
          this.generalStore.errorSnackbar("Erro ao atualizar tipo de mídia.");
        });
    },
    async deleteMediaType(mediatype) {
      mediatype.loadingDelete = true;
      await axios
        .request({
          url: `api/mediatypes/${mediatype.mediatype_id}`,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          mediatype.loadingDelete = false;
          this.generalStore.successSnackbar(
            "Tipo de mídia eliminado com sucesso."
          );
          this.getMediaTypes();
        })
        .catch((error) => {
          mediatype.loadingDelete = false;
          console.log(error);
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },
  },
};
</script>

<style></style>
