<template>
  <v-card :loading="isLoading">
    <v-card-title class="d-flex align-center justify-start pl-2">
      <v-btn variant="plain" color="primary" icon @click="show = !show">
        <v-icon>{{ show ? "mdi-chevron-down" : "mdi-chevron-right" }}</v-icon>
        <v-tooltip activator="parent" location="left">
          <span>{{ show ? "Ocultar Tipos" : "Mostrar Tipos" }}</span>
        </v-tooltip>
      </v-btn>
      <v-icon>mdi-alert-circle</v-icon>
      <span class="ml-3">Tipos de Evento</span>
    </v-card-title>
    <v-divider></v-divider>
    <v-expand-transition>
      <div v-show="show">
        <v-fab
          :active="show"
          class="me-4"
          color="success"
          location="top end"
          absolute
          offset
          append-icon="mdi-plus"
          @click="openAddDialog"
        >
          <span>Novo tipo</span>
        </v-fab>
        <v-card-text>
          <div v-if="isLoading">
            <v-row>
              <v-col cols="12" class="d-flex justify-center">
                <v-progress-circular
                  indeterminate
                  color="primary"
                ></v-progress-circular>
              </v-col>
            </v-row>
          </div>
          <div v-else-if="eventtypes.length > 0 && !isLoading">
            <v-list lines="1" class="py-0">
              <div
                v-for="(eventtype, index) in eventtypes"
                :key="eventtype.eventtype_id"
              >
                <v-list-item>
                  <v-list-item-title
                    class="d-flex align-center justify-space-between"
                  >
                    <div class="d-flex align-center">
                      <span class="text-subtitle-1 font-weight-medium">{{
                        eventtype.eventtype_name
                      }}</span>
                      <v-btn
                        color="warning"
                        variant="text"
                        icon
                        size="small"
                        @click="openEditTypeDialog(eventtype)"
                      >
                        <v-icon>mdi-pencil</v-icon>
                        <v-tooltip activator="parent" location="bottom"
                          >Editar</v-tooltip
                        >
                      </v-btn>
                    </div>
                    <div>
                      <v-btn
                        color="error"
                        variant="text"
                        icon
                        size="small"
                        @click="deleteEventType(eventtype)"
                        :loading="eventtype.loadingDelete"
                      >
                        <v-icon>mdi-close</v-icon>
                        <v-tooltip activator="parent" location="bottom"
                          >Apagar</v-tooltip
                        >
                      </v-btn>
                    </div>
                  </v-list-item-title>
                  <v-list-item-subtitle>
                    <span
                      >Importância
                      {{ getImportance(eventtype.eventtype_importance) }}</span
                    >
                  </v-list-item-subtitle>
                </v-list-item>
                <v-divider v-show="index != eventtypes.length - 1"></v-divider>
              </div>
            </v-list>
          </div>
          <div v-else-if="eventtypes.length == 0 && !isLoading">
            <v-row>
              <v-col cols="12" class="d-flex justify-center">
                <span>Nenhum tipo de evento registado.</span>
              </v-col>
            </v-row>
          </div>
        </v-card-text>
      </div>
    </v-expand-transition>
    <v-dialog v-model="dialog" @afterLeave="reset" width="auto">
      <v-card class="mx-auto" min-width="500">
        <v-card-title v-if="!editMode" class="bg-success">
          <span>Novo tipo de Mídia</span>
        </v-card-title>
        <v-card-text class="d-flex align-center justify-center"
          ><v-sheet width="500">
            <v-form class="mt-4" v-model="formValid">
              <v-row justify="center">
                <v-col cols="8">
                  <v-text-field
                    v-model.trim="eventType.eventtype_name"
                    label="Nome"
                    placeholder="Insira o nome"
                    prepend-inner-icon="mdi-tag-text-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.oneWord, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
                <v-col cols="4">
                  <v-select
                    v-model.number="eventType.eventtype_importance"
                    :items="importanceOptions"
                    item-title="title"
                    item-value="value"
                    label="Importância"
                    placeholder="Selecione a importância"
                    prepend-inner-icon="mdi-alert-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required]"
                  ></v-select>
                </v-col>
              </v-row>
            </v-form> </v-sheet
        ></v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="dialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingDialog"
            :disabled="editMode ? !canEditType : !formValid"
            @click="() => (editMode ? editEventType() : postEventType())"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "EventTypesConfig",
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      show: false,
      eventtypes: [],
      isLoading: false,

      editMode: false,

      dialog: false,
      eventType: {},
      formValid: false,
      isLoadingDialog: false,

      oldEventType: {},

      rules: {
        required: (value) => !!value || "Campo obrigatório",
        sizeSmall: (value) =>
          value.length <= 50 || "Máximo de 50 caracteres permitidos",
      },

      importanceOptions: [
        { title: "Baixa", value: 3 },
        { title: "Média", value: 2 },
        { title: "Alta", value: 1 },
      ],
    };
  },
  watch: {
    show(newVal) {
      if (newVal && this.eventtypes.length === 0) {
        this.getEventTypes();
      }
    },
  },
  computed: {
    canEditType() {
      return (
        (this.formValid &&
          this.eventType.eventtype_name.trim() !==
            this.oldEventType.eventtype_name.trim()) ||
        this.eventType.eventtype_importance !==
          this.oldEventType.eventtype_importance
      );
    },
  },
  methods: {
    getImportance(value) {
      console.log(value);
      return this.importanceOptions.find((option) => option.value === value)
        ?.title;
    },
    async getEventTypes() {
      this.isLoading = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/eventtypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.eventtypes = response.data;
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao obter tipos de eventos.");
        });
    },
    openAddDialog() {
      this.editMode = false;
      this.dialog = true;
    },
    reset() {
      this.eventType = {};
      this.oldEventType = {};
      this.formValid = false;
      this.isLoadingDialog = false;
    },
    async postEventType() {
      this.isLoadingDialog = true;
      let payload = {
        eventtype_name: this.eventType.eventtype_name.trim(),
        eventtype_importance: this.eventType.eventtype_importance,
      };
      await axios
        .request({
          url: "api/eventtypes",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingDialog = false;
          this.generalStore.successSnackbar(
            "Tipo de evento adicionado com sucesso."
          );
          this.dialog = false;
          this.getEventTypes();
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingDialog = false;
          this.generalStore.errorSnackbar("Erro ao adicionar tipo de evento.");
        });
    },

    openEditTypeDialog(eventtype) {
      this.editMode = true;
      this.eventType = { ...eventtype };
      this.oldEventType = { ...eventtype };
      this.dialog = true;
    },

    async editEventType() {
      this.isLoadingDialog = true;
      let payload = {
        eventtype_name: this.eventType.eventtype_name.trim(),
        eventtype_importance: this.eventType.eventtype_importance,
      };
      await axios
        .request({
          url: `api/eventtypes/${this.eventType.eventtype_id}`,
          method: "PATCH",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingDialog = false;
          this.generalStore.successSnackbar(
            "Tipo de evento editado com sucesso."
          );
          this.dialog = false;
          this.getEventTypes();
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingDialog = false;
          this.generalStore.errorSnackbar("Erro ao editar tipo de evento.");
        });
    },
    async deleteEventType(eventtype) {
      eventtype.loadingDelete = true;
      await axios
        .request({
          url: `api/eventtypes/${eventtype.eventtype_id}`,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          eventtype.loadingDelete = false;
          this.generalStore.successSnackbar(
            "Tipo de evento apagado com sucesso."
          );
          this.getEventTypes();
        })
        .catch((error) => {
          eventtype.loadingDelete = false;
          console.log(error);
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },
  },
};
</script>

<style></style>
