<template>
  <v-card :loading="isLoading">
    <v-card-title class="d-flex align-center justify-space-between pl-2 py-0">
      <div class="d-flex align-center">
        <v-btn variant="plain" color="primary" icon @click="show = !show">
          <v-icon>{{ show ? "mdi-chevron-down" : "mdi-chevron-right" }}</v-icon>
          <v-tooltip activator="parent" location="left">
            <span>{{
              show ? "Ocultar Variedades" : "Mostrar Variedades"
            }}</span>
          </v-tooltip>
        </v-btn>
        <span>{{ crop.crop_name }}</span>
        <v-btn color="warning" variant="text" icon @click="openEditCropDialog">
          <v-icon>mdi-pencil</v-icon>
          <v-tooltip activator="parent" location="bottom">Editar</v-tooltip>
        </v-btn>
      </div>
      <div>
        <v-btn
          color="error"
          variant="text"
          icon
          @click="deleteCrop"
          :loading="isLoadingDelete"
        >
          <v-icon>mdi-close</v-icon>
          <v-tooltip activator="parent" location="bottom">Apagar</v-tooltip>
        </v-btn>
      </div>
    </v-card-title>
    <v-card-subtitle class="mb-4 ml-10"
      ><span class="font-italic">
        {{ crop.crop_scientificname }}
      </span></v-card-subtitle
    >
    <v-divider></v-divider>
    <v-expand-transition>
      <div v-show="show">
        <v-fab
          :active="show"
          class="me-4"
          color="success"
          location="top end"
          absolute
          offset
          append-icon="mdi-plus"
          @click="openAddDialog"
        >
          <span>Nova Variedade</span>
        </v-fab>
        <v-card-text>
          <div v-if="isLoading">
            <v-row>
              <v-col cols="12" class="d-flex justify-center">
                <v-progress-circular
                  indeterminate
                  color="primary"
                ></v-progress-circular>
              </v-col>
            </v-row>
          </div>
          <div v-else-if="varieties.length > 0 && isLoading == false">
            <v-list lines="1" class="py-0">
              <div
                v-for="(variety, index) in varieties"
                :key="variety.variety_id"
              >
                <v-list-item>
                  <v-list-item-title
                    class="d-flex align-center justify-space-between"
                  >
                    <div class="d-flex align-center">
                      <span class="text-subtitle-1 font-weight-medium">{{
                        variety.variety_name
                      }}</span>
                      <v-btn
                        color="warning"
                        variant="text"
                        icon
                        size="small"
                        @click="openEditVarietyDialog(variety)"
                      >
                        <v-icon>mdi-pencil</v-icon>
                        <v-tooltip activator="parent" location="bottom"
                          >Editar</v-tooltip
                        >
                      </v-btn>
                    </div>
                    <div>
                      <v-btn
                        color="error"
                        variant="text"
                        icon
                        size="small"
                        @click="deleteVariety(variety)"
                        :loading="variety.loadingDelete"
                      >
                        <v-icon>mdi-close</v-icon>
                        <v-tooltip activator="parent" location="bottom"
                          >Apagar</v-tooltip
                        >
                      </v-btn>
                    </div>
                  </v-list-item-title>
                </v-list-item>
                <v-divider v-show="index != varieties.length - 1"></v-divider>
              </div>
            </v-list>
          </div>
          <div v-else class="d-flex justify-center">
            <span class="text-overline">Sem variedades</span>
          </div>
        </v-card-text>
      </div>
    </v-expand-transition>
    <v-dialog v-model="dialog" @afterLeave="reset" width="auto">
      <v-card class="mx-auto" min-width="500">
        <v-card-title v-if="!editMode" class="bg-success">
          ><span class="text-h6">Nova Variedade</span></v-card-title
        >
        <v-card-text class="d-flex align-center justify-center">
          <v-sheet width="400">
            <v-form class="mt-4" v-model="formValid">
              <v-row justify="center">
                <v-col cols="10">
                  <v-text-field
                    v-model.trim="variety.variety_name"
                    label="Nome"
                    placeholder="Insira o nome"
                    prepend-inner-icon="mdi-tag-text-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-sheet>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="dialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingDialog"
            :disabled="editMode ? !canEditVariety : !formValid"
            @click="() => (editMode ? editVariety() : postVariety())"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="editCropDialog" @afterLeave="resetEditCrop" width="auto">
      <v-card class="mx-auto" min-width="500">
        <v-card-text class="d-flex align-center justify-center">
          <v-sheet width="400">
            <v-form class="mt-4" v-model="editCropFormValid">
              <v-row justify="center">
                <v-col cols="10">
                  <v-text-field
                    v-model="cropEdit.crop_name"
                    label="Nome Comum"
                    placeholder="Insira o nome da cultura"
                    prepend-inner-icon="mdi-tag-text-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
                <v-col cols="10">
                  <v-text-field
                    v-model="cropEdit.crop_scientificname"
                    label="Nome Científico"
                    placeholder="Insira o nome científico da cultura"
                    prepend-inner-icon="mdi-flask-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-sheet>
        </v-card-text>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="editCropDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingEditCrop"
            :disabled="!canEditCrop"
            @click="editCrop()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "VarietyConfig",
  props: {
    crop: {
      type: Object,
      required: true,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      show: false,
      varieties: [],
      isLoading: false,

      editMode: false,

      dialog: false,
      variety: {},
      formValid: false,
      isLoadingDialog: false,

      //edit crop
      editCropDialog: false,
      cropEdit: {},
      editCropFormValid: false,
      isLoadingEditCrop: false,

      //delete crop
      isLoadingDelete: false,

      //edit variety
      oldVariety: {},

      rules: {
        required: (value) => !!value || "Campo obrigatório.",
        sizeSmall: (value) => value.length <= 25 || "Máximo de 25 caracteres.",
      },
    };
  },
  watch: {
    show(newVal) {
      if (newVal && this.varieties.length === 0) {
        this.getVarieties();
      }
    },
  },
  computed: {
    canEditCrop() {
      return (
        this.editCropFormValid &&
        (this.cropEdit.crop_name.trim() !== this.crop.crop_name.trim() ||
          this.cropEdit.crop_scientificname.trim() !==
            this.crop.crop_scientificname.trim())
      );
    },
    canEditVariety() {
      return (
        this.formValid &&
        this.oldVariety.variety_name.trim() !== this.variety.variety_name.trim()
      );
    },
  },
  methods: {
    async getVarieties() {
      this.isLoading = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/crops/" + this.crop.crop_id + "/varieties",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.varieties = response.data;
          this.isLoading = false;
        })
        .catch((error) => {
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao obter variedades.");
        });
    },
    openAddDialog() {
      this.editMode = false;
      this.dialog = true;
    },
    reset() {
      this.variety = {};
      this.oldVariety = {};
      this.formValid = false;
      this.isLoadingDialog = false;
    },
    async postVariety() {
      this.isLoadingDialog = true;
      let payload = {
        crop_id: this.crop.crop_id,
        variety_name: this.variety.variety_name.trim(),
      };
      await axios
        .request({
          url: "api/varieties",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingDialog = false;
          this.generalStore.successSnackbar(
            "Variedade adicionada com sucesso."
          );
          this.dialog = false;
          this.getVarieties();
        })
        .catch((error) => {
          this.isLoadingDialog = false;
          this.generalStore.errorSnackbar("Erro ao adicionar variedade.");
        });
    },
    openEditVarietyDialog(variety) {
      this.editMode = true;
      this.oldVariety = { ...variety };
      this.variety = { ...variety };
      this.dialog = true;
    },
    editVariety() {
      this.isLoadingDialog = true;
      let payload = {
        variety_name: this.variety.variety_name.trim(),
      };
      axios
        .request({
          url: "api/varieties/" + this.variety.variety_id,
          method: "PATCH",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then(() => {
          this.isLoadingDialog = false;
          this.generalStore.successSnackbar("Variedade editada com sucesso.");
          this.dialog = false;
          this.getVarieties();
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingDialog = false;
          this.generalStore.errorSnackbar("Erro ao editar variedade.");
        });
    },

    async deleteVariety(variety) {
      variety.loadingDelete = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/varieties/" + variety.variety_id,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then(() => {
          variety.loadingDelete = false;
          this.generalStore.successSnackbar("Variedade apagada com sucesso.");
          this.getVarieties();
        })
        .catch((error) => {
          variety.loadingDelete = false;
          console.error(error);
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },

    openEditCropDialog() {
      this.editCropDialog = true;
      this.editCropFormValid = true;
      this.cropEdit = { ...this.crop };
    },
    resetEditCrop() {
      this.cropEdit = {};
    },
    editCrop() {
      this.isLoadingEditCrop = true;
      let payload = {
        crop_name: this.cropEdit.crop_name.trim(),
        crop_scientificname: this.cropEdit.crop_scientificname.trim(),
      };
      axios
        .request({
          url: "api/crops/" + this.cropEdit.crop_id,
          method: "PATCH",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then(() => {
          this.isLoadingEditCrop = false;
          this.dialog = false;
          this.generalStore.successSnackbar("Cultura editada com sucesso.");
          this.$emit("refresh");
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingEditCrop = false;
          this.generalStore.errorSnackbar("Erro ao editar cultura.");
        });
    },
    deleteCrop() {
      this.isLoadingDelete = true;
      axios
        .request({
          url: "api/crops/" + this.crop.crop_id,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then(() => {
          this.isLoadingDelete = false;
          this.generalStore.successSnackbar("Cultura apagada com sucesso.");
          this.$emit("refresh");
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingDelete = false;
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },
  },
};
</script>

<style></style>
