<template>
  <v-card :loading="isLoading">
    <v-card-title class="d-flex align-center justify-start pl-2">
      <v-btn variant="plain" color="primary" icon @click="show = !show">
        <v-icon>{{ show ? "mdi-chevron-down" : "mdi-chevron-right" }}</v-icon>
        <v-tooltip activator="parent" location="left">
          <span>{{ show ? "Ocultar Tipos" : "Mostrar Tipos" }}</span>
        </v-tooltip>
      </v-btn>
      <v-icon>mdi-land-plots</v-icon>
      <span class="ml-3">Tipos de Parcela</span>
    </v-card-title>
    <v-divider></v-divider>
    <v-expand-transition>
      <div v-show="show">
        <v-fab
          :active="show"
          class="me-4"
          color="success"
          location="top end"
          absolute
          offset
          append-icon="mdi-plus"
          @click="openAddDialog"
        >
          <span>Novo tipo</span>
        </v-fab>
        <v-card-text>
          <div v-if="isLoading">
            <v-row>
              <v-col cols="12" class="d-flex justify-center">
                <v-progress-circular
                  indeterminate
                  color="primary"
                ></v-progress-circular>
              </v-col>
            </v-row>
          </div>
          <div v-else-if="regiontypes.length > 0 && isLoading == false">
            <v-list lines="1" class="py-0">
              <div
                v-for="(regiontype, index) in regiontypes"
                :key="regiontype.regiontype_id"
              >
                <v-list-item>
                  <v-list-item-title
                    class="d-flex align-center justify-space-between"
                  >
                    <div class="d-flex align-center">
                      <span class="text-subtitle-1 font-weight-medium">{{
                        regiontype.regiontype_name
                      }}</span>
                      <v-btn
                        color="warning"
                        variant="text"
                        icon
                        size="small"
                        @click="openEditTypeDialog(regiontype)"
                      >
                        <v-icon>mdi-pencil</v-icon>
                        <v-tooltip activator="parent" location="bottom"
                          >Editar</v-tooltip
                        >
                      </v-btn>
                    </div>
                    <div>
                      <v-btn
                        color="error"
                        variant="text"
                        icon
                        size="small"
                        @click="deleteRegionType(regiontype)"
                        :loading="regiontype.loadingDelete"
                      >
                        <v-icon>mdi-close</v-icon>
                        <v-tooltip activator="parent" location="bottom"
                          >Apagar</v-tooltip
                        >
                      </v-btn>
                    </div>
                  </v-list-item-title>
                </v-list-item>
                <v-divider v-show="index != regiontypes.length - 1"></v-divider>
              </div>
            </v-list>
          </div>
          <div v-else-if="regiontypes.length == 0 && isLoading == false">
            <v-row>
              <v-col cols="12" class="d-flex justify-center">
                <span>Nenhum tipo de parcela cadastrado.</span>
              </v-col>
            </v-row>
          </div>
        </v-card-text>
      </div>
    </v-expand-transition>
    <v-dialog v-model="dialog" @afterLeave="reset" width="auto">
      <v-card class="mx-auto" min-width="500">
        <v-card-title v-if="!editMode" class="bg-success">
          <span>Novo tipo de Parcela</span>
        </v-card-title>
        <v-card-text class="d-flex align-center justify-center">
          <v-sheet width="400">
            <v-form class="mt-4" v-model="formValid">
              <v-row justify="center">
                <v-col cols="10">
                  <v-text-field
                    v-model.trim="regionType.regiontype_name"
                    label="Nome"
                    placeholder="Insira o nome"
                    prepend-inner-icon="mdi-tag-text-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-sheet>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="dialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingDialog"
            :disabled="editMode ? !canEditType : !formValid"
            @click="() => (editMode ? editRegionType() : postRegionType())"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "RegionTypesConfig",
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      show: false,
      regiontypes: [],
      isLoading: false,

      editMode: false,

      dialog: false,
      regionType: {},
      formValid: false,
      isLoadingDialog: false,

      oldRegionType: {},

      rules: {
        required: (value) => !!value || "Campo requerido",
        sizeSmall: (value) => value.length <= 50 || "Máximo de 30 caracteres",
      },
    };
  },
  watch: {
    show(newVal) {
      if (newVal && this.regiontypes.length === 0) {
        this.getRegionTypes();
      }
    },
  },
  computed: {
    canEditType() {
      return (
        this.formValid &&
        this.oldRegionType.regiontype_name.trim() !==
          this.regionType.regiontype_name.trim()
      );
    },
  },
  methods: {
    async getRegionTypes() {
      this.isLoading = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      axios
        .request({
          url: "api/regiontypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.regiontypes = response.data;
          this.isLoading = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao obter tipos de região.");
        });
    },
    openAddDialog() {
      this.editMode = false;
      this.dialog = true;
    },
    reset() {
      this.regionType = {};
      this.oldRegionType = {};
      this.formValid = false;
      this.isLoadingDialog = false;
    },
    async postRegionType() {
      this.isLoadingDialog = true;
      let payload = {
        regiontype_name: this.regionType.regiontype_name.trim(),
      };
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      axios
        .request({
          url: "api/regiontypes",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingDialog = false;
          this.dialog = false;
          this.getRegionTypes();
          this.generalStore.successSnackbar(
            "Tipo de parcela criado com sucesso."
          );
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingDialog = false;
          this.generalStore.errorSnackbar("Erro ao criar tipo de parcela.");
        });
    },

    openEditTypeDialog(regiontype) {
      this.editMode = true;
      this.oldRegionType = { ...regiontype };
      this.regionType = { ...regiontype };
      this.dialog = true;
    },
    async editRegionType() {
      this.isLoadingDialog = true;
      let payload = {
        regiontype_name: this.regionType.regiontype_name.trim(),
      };
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: `api/regiontypes/${this.regionType.regiontype_id}`,
          method: "PATCH",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingDialog = false;
          this.generalStore.successSnackbar(
            "Tipo de parcela editado com sucesso."
          );
          this.dialog = false;
          this.getRegionTypes();
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingDialog = false;
          this.generalStore.errorSnackbar("Erro ao editar tipo de parcela.");
        });
    },

    async deleteRegionType(regiontype) {
      regiontype.loadingDelete = true;
      await axios
        .request({
          url: `api/regiontypes/${regiontype.regiontype_id}`,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          regiontype.loadingDelete = false;
          this.generalStore.successSnackbar(
            "Tipo de parcela deletado com sucesso."
          );
          this.getRegionTypes();
        })
        .catch((error) => {
          regiontype.loadingDelete = false;
          console.error(error);
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },
  },
};
</script>

<style></style>
