<template>
  <v-card :loading="isLoading">
    <v-card-title class="d-flex align-center justify-space-between pl-2 py-0">
      <div class="d-flex align-center">
        <v-btn variant="plain" color="primary" icon @click="show = !show">
          <v-icon>{{ show ? "mdi-chevron-down" : "mdi-chevron-right" }}</v-icon>
          <v-tooltip activator="parent" location="left">
            <span>{{ show ? "Ocultar Inimigos" : "Mostrar Inimigos" }}</span>
          </v-tooltip>
        </v-btn>
        <span>{{ enemytype.enemytype_name }}</span>
        <v-btn color="warning" variant="text" icon @click="openEditTypeDialog">
          <v-icon>mdi-pencil</v-icon>
          <v-tooltip activator="parent" location="bottom">Editar</v-tooltip>
        </v-btn>
      </div>
      <div>
        <v-btn
          color="error"
          variant="text"
          icon
          @click="deleteEnemyType"
          :loading="isLoadingDelete"
        >
          <v-icon>mdi-close</v-icon>
          <v-tooltip activator="parent" location="bottom">Apagar</v-tooltip>
        </v-btn>
      </div>
    </v-card-title>
    <v-divider></v-divider>
    <v-expand-transition>
      <div v-show="show">
        <v-fab
          :active="show"
          class="me-4"
          color="success"
          location="top end"
          absolute
          offset
          append-icon="mdi-plus"
          @click="openAddDialog"
        >
          <span>Novo Inimigo</span>
        </v-fab>
        <v-card-text>
          <div v-if="isLoading">
            <v-row>
              <v-col cols="12" class="d-flex justify-center">
                <v-progress-circular
                  indeterminate
                  color="primary"
                ></v-progress-circular>
              </v-col>
            </v-row>
          </div>
          <div v-else-if="enemies.length > 0 && isLoading == false">
            <v-list lines="1" class="py-0">
              <div v-for="(enemy, index) in enemies" :key="enemy.enemy_id">
                <v-list-item>
                  <v-list-item-title
                    class="d-flex align-center justify-space-between"
                  >
                    <div class="d-flex align-center">
                      <span class="text-subtitle-1 font-weight-medium">{{
                        enemy.enemy_name
                      }}</span>
                      <v-btn
                        color="warning"
                        variant="text"
                        icon
                        size="small"
                        @click="openEditEnemyDialog(enemy)"
                      >
                        <v-icon>mdi-pencil</v-icon>
                        <v-tooltip activator="parent" location="bottom"
                          >Editar</v-tooltip
                        >
                      </v-btn>
                    </div>
                    <div>
                      <v-btn
                        color="error"
                        variant="text"
                        icon
                        size="small"
                        @click="deleteEnemy(enemy)"
                        :loading="enemy.loadingDelete"
                      >
                        <v-icon>mdi-close</v-icon>
                        <v-tooltip activator="parent" location="bottom"
                          >Apagar</v-tooltip
                        >
                      </v-btn>
                    </div>
                  </v-list-item-title>
                </v-list-item>
                <v-divider v-show="index != enemies.length - 1"></v-divider>
              </div>
            </v-list>
          </div>
          <div v-else class="d-flex justify-center">
            <span class="text-overline">Sem inimigos</span>
          </div>
        </v-card-text>
      </div>
    </v-expand-transition>
    <v-dialog v-model="dialog" @afterLeave="reset" width="auto">
      <v-card class="mx-auto" min-width="500">
        <v-card-title v-if="!editMode" class="bg-success"
          ><span class="text-h6">Novo Inimigo</span></v-card-title
        >
        <v-card-text class="d-flex align-center justify-center">
          <v-sheet width="400">
            <v-form class="mt-4" v-model="formValid">
              <v-row justify="center">
                <v-col cols="10">
                  <v-text-field
                    v-model.trim="enemy.enemy_name"
                    label="Nome"
                    placeholder="Insira o nome"
                    prepend-inner-icon="mdi-tag-text-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-sheet>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="dialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingDialog"
            :disabled="editMode ? !canEditEnemy : !formValid"
            @click="() => (editMode ? editEnemy() : postEnemy())"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="editTypeDialog" @afterLeave="resetEditType" width="auto">
      <v-card class="mx-auto" min-width="500">
        <v-card-text class="d-flex align-center justify-center">
          <v-sheet width="400">
            <v-form class="mt-4" v-model="typeFormValid">
              <v-row justify="center">
                <v-col cols="10">
                  <v-text-field
                    v-model.trim="enemyTypeEdit.enemytype_name"
                    label="Nome"
                    placeholder="Insira o nome"
                    prepend-inner-icon="mdi-tag-text-outline"
                    variant="outlined"
                    density="comfortable"
                    :rules="[rules.required, rules.sizeSmall]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-sheet>
        </v-card-text>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="editTypeDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingEditType"
            :disabled="!canEditType"
            @click="editEnemyType"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "EnemiesConfig",
  props: {
    enemytype: {
      type: Object,
      required: true,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      show: false,
      enemies: [],
      isLoading: false,

      editMode: false,

      dialog: false,
      enemy: {},
      formValid: false,
      isLoadingDialog: false,

      //edit enemy type
      editTypeDialog: false,
      enemyTypeEdit: {},
      typeFormValid: false,
      isLoadingEditType: false,

      //delete enemy type
      isLoadingDelete: false,

      //edit enemy
      oldEnemy: {},

      rules: {
        required: (value) => !!value || "Campo obrigatório",
        sizeSmall: (value) => value?.length <= 30 || "Máximo de 30 caracteres",
      },
    };
  },
  watch: {
    show(newVal) {
      if (newVal && this.enemies.length === 0) {
        this.getEnemies();
      }
    },
  },
  computed: {
    canEditType() {
      return (
        this.typeFormValid &&
        this.enemyTypeEdit.enemytype_name.trim() !==
          this.enemytype.enemytype_name.trim()
      );
    },
    canEditEnemy() {
      return (
        this.formValid &&
        this.enemy.enemy_name.trim() !== this.oldEnemy.enemy_name.trim()
      );
    },
  },
  methods: {
    async getEnemies() {
      this.isLoading = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: "api/enemies",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: { enemytype_id: this.enemytype.enemytype_id },
        })
        .then((response) => {
          this.enemies = response.data;
          this.isLoading = false;
        })
        .catch((error) => {
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao buscar inimigos");
        });
    },
    openAddDialog() {
      this.editMode = false;
      this.dialog = true;
    },
    reset() {
      this.enemy = {};
      this.oldEnemy = {};
      this.formValid = false;
      this.isLoadingDialog = false;
    },
    async postEnemy() {
      this.isLoadingDialog = true;
      let payload = {
        enemytype_id: this.enemytype.enemytype_id,
        enemy_name: this.enemy.enemy_name.trim(),
      };
      await axios
        .request({
          url: "api/enemies",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingDialog = false;
          this.generalStore.successSnackbar("Inimigo adicionado com sucesso.");
          this.dialog = false;
          this.getEnemies();
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingDialog = false;
          this.generalStore.errorSnackbar("Erro ao adicionar inimigo.");
        });
    },

    openEditEnemyDialog(enemy) {
      this.editMode = true;
      this.oldEnemy = { ...enemy };
      this.enemy = { ...enemy };
      this.dialog = true;
    },

    editEnemy() {
      this.isLoadingDialog = true;
      let payload = {
        enemy_name: this.enemy.enemy_name.trim(),
      };
      axios
        .request({
          url: `api/enemies/${this.enemy.enemy_id}`,
          method: "PATCH",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingDialog = false;
          this.generalStore.successSnackbar("Inimigo editado com sucesso.");
          this.dialog = false;
          this.getEnemies();
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingDialog = false;
          this.generalStore.errorSnackbar("Erro ao editar inimigo.");
        });
    },

    async deleteEnemy(enemy) {
      enemy.loadingDelete = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: `api/enemies/${enemy.enemy_id}`,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          enemy.loadingDelete = false;
          this.generalStore.successSnackbar("Inimigo apagado com sucesso.");
          this.getEnemies();
        })
        .catch((error) => {
          console.log(error);
          enemy.loadingDelete = false;
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },

    openEditTypeDialog() {
      this.editTypeDialog = true;
      this.enemyTypeEdit = { ...this.enemytype };
    },
    resetEditType() {
      this.enemyTypeEdit = {};
    },
    editEnemyType() {
      this.isLoadingEditType = true;
      let payload = {
        enemytype_name: this.enemyTypeEdit.enemytype_name.trim(),
      };
      axios
        .request({
          url: `api/enemytypes/${this.enemyTypeEdit.enemytype_id}`,
          method: "PATCH",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingEditType = false;
          this.generalStore.successSnackbar(
            "Tipo de inimigo editado com sucesso."
          );
          this.editTypeDialog = false;
          this.$emit("refresh");
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingEditType = false;
          this.generalStore.errorSnackbar("Erro ao editar tipo de inimigo.");
        });
    },

    async deleteEnemyType() {
      this.isLoadingDelete = true;
      await new Promise((resolve) => setTimeout(resolve, 2000));
      await axios
        .request({
          url: `api/enemytypes/${this.enemytype.enemytype_id}`,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.isLoadingDelete = false;
          this.generalStore.successSnackbar(
            "Tipo de inimigo apagado com sucesso."
          );
          this.$emit("refresh");
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingDelete = false;
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },
  },
};
</script>

<style></style>
